body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #61dafb;
  height: 100vh;
  overflow-y: auto;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  position: -webkit-sticky;
  position: sticky;
  z-index: 800;
  width: 100%;
  top: 0;
  background-color: #282c34;
  min-height: 5vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.basic-button{
  color: black;
  text-decoration: none;
  background-color: #61dafb;
  border-color: #754fa0;
  border-radius: 5vh;
  font-weight: 600;
  justify-content: center;
}

.link-button{
  justify-content: center;
  display: flex;

}

.App-link {
  color: #61dafb;
}

.App-body{
  position: relative;
  z-index: 700;
  background-color: #61dafb;
  margin-top: 5vh;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.route-wrapper {
  position: relative;
  z-index: 100;
  display: flex;
  justify-content: center;
  width: 100%;
}

.route-wrapper > div {
  position: absolute;
  display: flex;
  justify-content: center;
}

.headshot {
    height: 20vh;
    width: 20vh;
    border: 5px solid blue;
    border-radius: 5vh;
    margin: 1vh;
    display: inline-block;
    align-content: center;
    position: relative;
}

.name-box{
    opacity: 0;
    display: flex;
    position: relative;
    border-radius: 4vh;
    font-size: 2vh;
    background-color: #6bbde4;
    z-index: 200;
    width: 100%;
    height: 100%;
    -webkit-animation: fadeOut .5s ease-in-out;
            animation: fadeOut .5s ease-in-out;
}

.image-box{
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    border-radius: 4vh;
    background-color: #6bbde4;
    -webkit-transform: translateY(-100%);
            transform: translateY(-100%);
    z-index: 100;
    width: 100%;
    height: 100%;
}

.invisible{
    visibility: hidden;
}

.name-box:hover{
    display: flex;
    background-color: #6bbde4;
    z-index: 200;
    width: 100%;
    height: 100%;
    opacity: 1;
    -webkit-animation: fadeIn .25s ease-in-out;
            animation: fadeIn .25s ease-in-out;
}

.name-focus{
    opacity: 0;
    color: black;
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 300;
    -webkit-animation: fadeOut .25s ease-in-out;
            animation: fadeOut .25s ease-in-out;
}

.name-focus:hover{
    width: 100%;
    height: 100%;
    opacity: 1;
    z-index: 300;
    -webkit-animation: fadeIn .5s ease-in-out;
            animation: fadeIn .5s ease-in-out;
}

@-webkit-keyframes fadeOut {
    0%{
    opacity: 1;
    }
    40%{
    opacity: .6;
    }
    100% {
    opacity: 0;
    }
    20% {
    opacity: .8;
    }
    60%{
    opacity: .4;
    }
    80%{
    opacity: .2;
    }
}

@keyframes fadeOut {
    0%{
    opacity: 1;
    }
    40%{
    opacity: .6;
    }
    100% {
    opacity: 0;
    }
    20% {
    opacity: .8;
    }
    60%{
    opacity: .4;
    }
    80%{
    opacity: .2;
    }
}

@-webkit-keyframes fadeIn {
    0%{
    opacity: 0;
    }
    40%{
    opacity: .4;
    }
    100% {
    opacity: 1;
    }
    20% {
    opacity: .2;
    }
    60%{
    opacity: .6;
    }
    80%{
    opacity: .8;
    }
}

@keyframes fadeIn {
    0%{
    opacity: 0;
    }
    40%{
    opacity: .4;
    }
    100% {
    opacity: 1;
    }
    20% {
    opacity: .2;
    }
    60%{
    opacity: .6;
    }
    80%{
    opacity: .8;
    }
}
.result-page{
        display: flex;
        position: relative;
        justify-content: left;
        flex-direction: column;
        margin: 2vh;
        min-height: 80vh;
        min-width: 60vh;
        border-radius: 8vh;
        z-index: -1;
        background-color: blue;
}

.text-field{
    width: 58vh;
    z-index: 200;
    border-radius: 8vh;
    background-color: #6bbde4;
    border: 1px solid black;
    box-shadow: 2px 2px black;
}

.inner-information{
    display: flex;
    justify-content: left;
    flex-direction: column;
    margin: 2vh;
}

.info-field{
    margin: 2vh;
    display: flex;
    justify-content: left;
    color: black;
}

.relevant-image-vertical{
    display: flex;
    height: 50vh;
    z-index: 200;
    width: 40vh;
    border-radius: 8vh;
    background-color: inherit;
    box-shadow: 2px 2px black;
}

.relevant-image-horizontal{
    display: flex;
    height: 40vh;
    z-index: 200;
    width: 60vh;
    border-radius: 8vh;
    background-color: inherit;
    box-shadow: 2px 2px black;
}

.full-results-page{
    width: 100%;
}
.left-move {
    width: 10vh;
    left: 0;
    height: 10vh;
    background-color: white;
    border: 10px solid white;
}
.right-move {
    margin: 10px;
    width: 10vh;
    left: 0;
    height: 10vh;
    background-color: white;
    border: 10px solid white;
}
.overview-page-outer {
    display: inline-flex;
    justify-content: center;
    margin: 2vh;
    height: 80vh;
    width: 60vh;
    border-radius: 8vh;
    z-index: 100;
    background-color: blue;
}

.overview-page-inner {
    margin: 1vh;
    height: 78vh;
    width: 58vh;
    z-index: 200;
    border-radius: 8vh;
    background-color: #282c34;
}
